.header{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: var(--body-color);
}
.nav{
    height: calc(var(--header-height) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
    padding: 0 10px 0;
}
.nav__logo,
.nav__toggle{
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.nav__list{
    display: flex;
    column-gap: 2rem;
}
.nav__link{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
    transition: .3s;

}
.nav__close,
.nav__toggle,
.nav__icon{
    display: none;
}
/* active link */
.active-link,
.nav__link:hover{
    color: var(--title-color-dark);
}

/* ==================== BREAKPOINTS ================ */

@media screen and (max-width: 768px) {
    .header{
        top: initial;
        bottom: 0;
    }
    .nav {
        height: var(--header-height);
        padding: 0 10px 0;
    }
    .nav__menu{
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: var(--body-color);
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 0 4px rgba(0, 0, 0 ,0.15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: 0.3s;
    }
    
    /* show menu */
    .show__menu{
        bottom: 0;

    }
    .nav__list{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }
    .nav__icon{
        font-size: 1.2rem;
    }
    .nav__close{
        position: absolute;
        right: 1.3rem;
        bottom: .5rem;
        font-size: 1.5rem;
        cursor: pointer;
        color: var(--title-color);
    }
    .nav__close:hover{
        color: var(--title-color-dark);
    }
    
    
    .nav__icon,
    .nav__close,
    .nav__toggle{
       display: block;
    }
}
@media screen and(max-width:px) {
 .nav__menu{
    padding: 2rem 0.25rem 4rem;

 }
 .nav__list{
    column-gap: 0;
 }
 
}